import React, { PureComponent } from 'react';
import axios from 'axios';
import { urlInterfaceGroup } from "../config/url.config";
import { connect } from 'react-redux';
import Loading from '../element/Loading';
import { getScrollTop, getScrollHeight, getClientHeight } from "../function/ScrollHandler";
import './DisplayTable.css';

function map(state) {
    return {
        comments: state.comments,
        isCommentRequesting: state.isCommentRequesting,
    }
}

class DisplayTable extends PureComponent {
    constructor(props){
        super(props);
        this.state = {
            last: -1,
        };
        this.commentsHandler = this.commentsHandler.bind(this);
        this.setComments = this.setComments.bind(this);
        this.setIsRequesting = this.setIsRequesting.bind(this);
        this.scrollHandler = this.scrollHandler.bind(this);
        this.setLast = this.setLast.bind(this);
    }
    setIsRequesting(v) {
        this.props.dispatch({
            type: 'SET_IS_COMMENT_REQUESTING',
            value: v,
        })
    }
    setLast(l) {
        this.setState({
            last: l,
        })
    }
    async scrollHandler(e) {
        e.stopPropagation();
        if(this.state.last > 0 && !this.props.isCommentRequesting && getClientHeight() + getScrollTop() + 160 > getScrollHeight()) {
            await this.setIsRequesting(true);
            await this.commentsHandler(this.state.last);
            await this.setIsRequesting(false);
        }
    }
    async commentsHandler(lastNumber) {
        let { 'data': { list, last } } = await axios.get(`${urlInterfaceGroup.commentList.interface}?${lastNumber ? `last=${lastNumber}` : '' }`);
        if(this.props.comments.length === 0) {

        }else {
            console.log(...this.props.comments);
            list = [...this.props.comments, ...list];
            console.log(list);
        }
        await this.setComments(list);
        this.setLast(last);
        await this.setIsRequesting(false);
    }
    setComments(comments) {
        this.props.dispatch({
            type: 'SET_COMMENT',
            value: comments,
        });
    }
    async componentDidMount() {
        window.addEventListener('scroll', this.scrollHandler);
        await this.commentsHandler();
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.scrollHandler);
    }
    render() {
        return (
            <div id="DisplayTable"
                 onMouseMove={this.mouseMoveHandler}
                 onMouseLeave={this.mouseLeaveHandler}
                 onMouseUp={this.mouseUpHandler}>
                <div className="window" onWheel={this.scrollHandler}>
                    <div className="container">
                        {
                            this.props.comments ? this.props.comments.map((item, index) => (
                                <div className="comment_sel"
                                    key={index}>
                                    <div className="content_box">
                                        <span>
                                            {
                                                item.content
                                            }
                                        </span>
                                    </div>
                                    <div className="time_box">
                                        <span>
                                            {
                                                item.time
                                            }
                                        </span>
                                    </div>
                                </div>
                            )) : ''
                        }
                    </div>
                    {
                        this.props.isCommentRequesting  ? (
                            <div className={'loading_box'}>
                                <Loading/>
                            </div>
                        ) : ''
                    }
                </div>

            </div>
        )
    }
}

export default connect(map)(DisplayTable);