import React from 'react';
import { Logo } from '../../element/Logo';
import SelList from '../../element/SelList';
import './Navigator.css';
function Navigator() {
    return (
        <div id="Navigator">
            <div className="logo_box">
                <Logo/>
            </div>
            <div className="sel_box">
                <SelList/>
            </div>
        </div>
    );
}

export default Navigator;